<template>
  <form :class="[
      {'form-loading': loading}
  ]">
    <Loader class="loader" v-if="loading"></Loader>
    <div class="title">Оформить заказ</div>
    <Input
        :disabled="!$store.getters.totalProducts || loading"
        type="text"
        name="name"
        placeholder="Екатерина"
        label="Ваше имя"
        v-model="form.name"
    />
    <Input
        :disabled="!$store.getters.totalProducts || loading"
        type="text"
        name="name"
        placeholder="+7 999 999 99 99"
        label="Ваш телефон"
        v-model="form.phone"
        mask="phone"
    />
    <Input
        :disabled="!$store.getters.totalProducts || loading"
        type="email"
        name="name"
        placeholder="example@mail.ru"
        label="ваш email"
        v-model="form.email"
    />
    <button
        :disabled="!$store.getters.totalProducts || loading || !isValid"
        class="btn"
        @click="submit"
    >
      заказать
    </button>
  </form>
</template>

<script>
import Input from "@/components/Input";
import Loader from "@/components/Loader";

export default {
  name: "Form",
  components: {Input, Loader},
  computed: {
    isValid() {
      return !!this.form.phone
    }
  },
  data: () => ({
    loading: false,
    form: {
      name: '',
      phone: '',
      email: ''
    }
  }),
  methods: {
    async submit() {
      this.loading = true
      await this.$store.dispatch('submit', this.form)
      await this.$store.dispatch('createNotification', 'Заказ успешно создан. Мы свяжемся с вами в ближайшее время')
      await this.$store.dispatch('fetchBasket')
      this.clearForm()
      this.loading = false
    },
    clearForm() {
      this.form = {
        name: '',
        phone: '',
        email: ''
      }
    }
  }
}
</script>

<style scoped>
.form-loading {
  position: relative;
}

.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .2);
  z-index: 10;
}
</style>