<template>
  <div
      :class="[
          'basket-item flex-between align-end',
          {'basket-item-loading': loading}
      ]"
  >
    <div class="left">
      <div class="title">{{ item.product.name }}</div>
      <QuantityInput @input="handleQuantityChange" v-model="item.quantity"></QuantityInput>
    </div>
    <div class="right">
      <div class="price">{{ item.product.price }} руб.</div>
      <div @click="removeFromBasket" class="delete">
        <span>Удалить</span>
        <svg viewBox="0 0 12 14" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path
              d="M8 0.333008H4C3.26467 0.333008 2.66667 0.931008 2.66667 1.66634V2.33301H0V3.66634H1.33333V12.333C1.33333 13.0683 1.93133 13.6663 2.66667 13.6663H9.33333C10.0687 13.6663 10.6667 13.0683 10.6667 12.333V3.66634H12V2.33301H9.33333V1.66634C9.33333 0.931008 8.73533 0.333008 8 0.333008ZM4 1.66634H8V2.33301H4V1.66634ZM9.33333 12.333H2.66667V3.66634H9.33333V12.333Z"
              fill="#EB5757"/>
          <path d="M4 4.99967H5.33333V10.9997H4V4.99967Z" fill="#EB5757"/>
          <path d="M6.66667 4.99967H8V10.9997H6.66667V4.99967Z" fill="#EB5757"/>
        </svg>
      </div>
    </div>
    <Loader class="loader" v-if="loading"></Loader>
  </div>
</template>

<script>
import _ from 'lodash'
import QuantityInput from "@/components/QuantityInput";
import Loader from "@/components/Loader";

export default {
  name: "BasketItem",
  components: {QuantityInput, Loader},
  props: ['item'],
  data: () => ({
    loading: false
  }),
  methods: {
    handleQuantityChange: _.debounce(function(quantity) {
      this.$store.dispatch('changeProductQuantity', {
        productId: this.item.id,
        quantity
      })
    }, 300),
    async removeFromBasket() {
      this.loading = true
      await this.$store.dispatch('removeProductFromBasket', this.item.id)
      await this.$store.dispatch('createNotification', 'Товар удален из корзины')
      this.loading = false
    }
  }
}
</script>

<style scoped>
.basket-item-loading {
  color: #abacb1 !important;
  position: relative;
}
.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .2);
  z-index: 10;
}
</style>